.RechargeBox {
  height: 100%;
  width: 100%;
  background-size: contain;
  background-repeat: no-repeat;
}
.RechargeBox .WordArt {
  font-family: "Yuanti SC";
  font-size: 14px;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: bolder;
  background-image: -webkit-gradient(linear, 0 0, 0 bottom, from(#6f4934), to(#6f4934));
}
.RechargeBox .publicbtn {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: #fafabe;
  font-weight: bold;
  text-shadow: 1px 0px 0px #bc8845, -1px 0px 0px #bc8845, 0px 1px 0px #bc8845, 0px -1px 0px #bc8845;
}
.RechargeBox .publicbtn span {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 4%;
}
.RechargeBox .withDrawCashBox_child1 {
  height: 15.7%;
  width: 100%;
  position: relative;
}
.RechargeBox .withDrawCashBox_child1 :nth-child(1) {
  position: absolute;
  left: 1%;
  top: 10%;
  width: 6%;
  height: 75%;
  background-size: contain;
  background-repeat: no-repeat;
}
.RechargeBox .withDrawCashBox_child1 .withDrawCashBox_child_title {
  width: 32.8%;
  height: 88%;
  background-size: contain;
  background-repeat: no-repeat;
  margin: 0 auto;
}
.RechargeBox .withDrawCashBox_child1 .withDrawCashBox_child_title :nth-child(1) {
  position: relative;
  width: 24%;
  height: 70%;
  margin: auto;
  background-size: contain;
  background-repeat: no-repeat;
}
.RechargeBox .withDrawCashBox_child2 {
  position: relative;
  width: 100%;
  height: 81%;
  padding-top: 1%;
  box-sizing: border-box;
}
.RechargeBox .withDrawCashBox_child2 .withDrawCashBox_child2_nav {
  position: relative;
  float: left;
  width: 17.6%;
  height: 87%;
  padding-top: 0%;
}
.RechargeBox .withDrawCashBox_child2 .withDrawCashBox_child2_nav div {
  position: relative;
  height: 18%;
  width: 85%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: contain;
  background-repeat: no-repeat;
  margin-top: 5%;
}
.RechargeBox .withDrawCashBox_child2 .withDrawCashBox_child2_nav .cur {
  transform: scale(1.16, 1);
  transform-origin: left center;
  background-repeat: no-repeat;
}
.RechargeBox .withDrawCashBox_child2 .withDrawCashBox_child2_nav .cur img {
  width: 90%;
  margin-right: 10%;
}
.RechargeBox .withDrawCashBox_child2 .withDrawCashBox_content {
  position: relative;
  float: left;
  width: 80.5%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  filter: alpha(opacity=60);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
}
.RechargeBox .withDrawCashBox_child2 .withDrawCashBox_content .withDrawCashBox_inner_box {
  position: relative;
  background: #d9f8fb;
  border-radius: 7px;
  width: 97.8%;
  height: 96%;
  opacity: 1;
  box-sizing: border-box;
  padding: 2%;
  box-shadow: 5px 0 2px #bbd6d9 inset, -5px 0 2px #bbd6d9 inset, 0px 5px 2px #bbd6d9 inset, 5px -5px 2px #bbd6d9 inset;
}
.RechargeBox .Alert-container {
  position: fixed;
  top: 0%;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 200;
  background: rgba(0, 0, 0, 0) !important;
  /* 兼容ie几不知道，好像ie5 */
  filter: alpha(opacity=0);
  z-index: 400;
}
.RechargeBox .Alert-child-container {
  position: relative;
  width: 30%;
  height: 14%;
  margin: auto;
  top: 40%;
  z-index: 250;
  /*z-index要放在父元素之上 */
  background-size: contain;
  background-repeat: no-repeat;
  background: #1e1531;
  display: flex;
  align-items: center;
  padding-top: 0;
  border-radius: 5px ;
  opacity: 0.9;
}
.RechargeBox .Alert-child {
  position: relative;
  top: 0%;
  width: 55%;
  margin-left: 5%;
  height: 100%;
  border-radius: 5px;
  float: left;
  z-index: 300;
  /*z-index要放在父元素之上 */
  text-align: center;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}
.RechargeBox .Alert-btn {
  float: left;
  width: 35%;
  height: 50%;
  position: relative;
  text-align: center;
}
.RechargeBox .Alert-btn :nth-child(1) {
  width: 100%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  float: left;
  background: #0098ff;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}
.div-container {
  position: fixed;
  top: 0%;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 200;
  background: rgba(0, 0, 0, 0.5) !important;
  /* 兼容ie几不知道，好像ie5 */
  background: #000;
  filter: alpha(opacity=60);
}
/*设置div-child的父元素主要是将要此元素的父元素透明特性继承过来，故div-child不会半透明，而是不透明，解决了父元素透明，子元素也透明的bug */
.div-child-container {
  position: relative;
  width: 52%;
  height: 56%;
  margin: auto;
  top: 26%;
  z-index: 250;
  /*z-index要放在父元素之上 */
  background: #d6c2bd;
}
.div-title {
  position: absolute;
  top: -10%;
  left: -5%;
  width: 110%;
  height: 19%;
  background-size: contain;
  background-repeat: no-repeat;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}
.div-title-child1 {
  width: 35%;
  height: 65%;
  background-size: contain;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #d6b36f;
}
.div-icon {
  position: absolute;
  top: -1%;
  right: -8%;
  width: 12%;
  height: 35%;
  background-size: contain;
  background-repeat: no-repeat;
}
.div-child {
  position: relative;
  top: 12%;
  width: 90%;
  height: 50%;
  margin: auto;
  z-index: 300;
  /*z-index要放在父元素之上 */
  text-align: center;
  background: #d6a67f;
  box-shadow: 0 5px 4px 0px #516267;
  border-radius: 5px;
  font-weight: bold;
}
.div-child .div-child_child1 {
  position: relative;
  top: 10%;
  width: 90%;
  height: 40%;
  padding-left: 8%;
  box-sizing: border-box;
  margin: 0 auto;
  text-align: start;
  color: #8c5c4a;
  display: flex;
  font-size: 14px;
  align-items: center;
}
.div-child .div-child_icon {
  position: absolute;
  left: 3%;
  top: 12%;
  width: 8%;
  height: 25%;
  background-size: contain;
  background-repeat: no-repeat;
}
.div-child .div-child_child2 {
  padding: 0;
  position: relative;
  top: 7%;
  width: 80%;
  height: 30%;
  margin: auto;
  margin-top: 2%;
  display: flex;
  align-items: center;
}
.div-child .div-child_child2 :nth-child(1) {
  top: 0;
  height: 70%;
  display: inline-block;
  align-items: center;
  width: 60%;
  float: left;
  text-align: center;
  padding: 0;
  color: white;
  font-size: 14px;
}
.div-child .div-child_child2 :nth-child(2) {
  height: 60%;
  top: 0;
  float: left;
  text-align: start;
  width: 30%;
  font-size: 14px;
  color: red;
}
.div-btn {
  width: 90%;
  height: 16%;
  position: relative;
  top: 25%;
  margin: auto;
  text-align: center;
}
.div-btn :nth-child(1) {
  margin-left: 13%;
  width: 30%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  float: left;
}
.div-btn :nth-child(2) {
  margin-left: 13%;
  width: 30%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  float: left;
}
.div-foot {
  position: absolute;
  bottom: -10%;
  left: -3%;
  width: 106%;
  height: 15%;
  background-size: contain;
  background-repeat: no-repeat;
}
