.MyOrderBgWhite {
  width: 96%;
  position: absolute;
  right: 0;
  top: 16%;
  height: 80%;
  left: 2%;
  overflow: hidden;
}
.MyOrderBgWhite .MyOrderBgWhite_child1 {
  width: 80%;
  height: 13%;
  margin-left: 3%;
  margin-top: 2%;
  margin-bottom: 1%;
  display: flex;
  align-items: flex-end;
}
.MyOrderBgWhite .MyOrderBgWhite_child1 div {
  margin: 0;
}
.MyOrderBgWhite .MyOrderBgWhite_child1 .MyOrderBgWhite_child1_child1 {
  width: 25%;
  height: 100%;
  float: left;
  background: #9f7463;
}
.MyOrderBgWhite .MyOrderBgWhite_child1 .MyOrderBgWhite_child1_child1 input {
  height: 90%;
  width: 80%;
  background: transparent;
  margin-left: 7%;
  outline: none;
  border: transparent;
  color: white;
}
.MyOrderBgWhite .MyOrderBgWhite_child1 .MyOrderBgWhite_child1_select {
  margin-left: 2%;
  width: 23%;
  height: 100%;
  position: relative;
  left: 0%;
  float: left;
  background: #9f7463;
}
.MyOrderBgWhite .MyOrderBgWhite_child1 .MyOrderBgWhite_child1_select i {
  font-style: normal;
  width: 20%;
  height: 60%;
  position: absolute;
  right: 1%;
  top: 20%;
  display: inline-block;
  background-size: contain;
  background-repeat: no-repeat;
}
.MyOrderBgWhite .MyOrderBgWhite_child1 .MyOrderBgWhite_child1_select form {
  width: 100%;
  position: relative;
  top: 15%;
  color: white;
}
.MyOrderBgWhite .MyOrderBgWhite_child1 .MyOrderBgWhite_child1_select form #divselect {
  position: relative;
  z-index: 100;
}
.MyOrderBgWhite .MyOrderBgWhite_child1 .MyOrderBgWhite_child1_select form #divselect cite {
  width: 85%;
  display: inline-block;
  margin-left: 5%;
  padding-left: 5%;
  font-style: normal;
  margin-bottom: 3%;
}
.MyOrderBgWhite .MyOrderBgWhite_child1 .MyOrderBgWhite_child1_select form #divselect ul {
  opacity: 0.8;
  list-style: none;
}
.MyOrderBgWhite .MyOrderBgWhite_child1 .MyOrderBgWhite_child1_select form #divselect ul li {
  width: 100%;
  height: 30%;
  background: #9f7463;
  display: flex;
  align-items: center;
  border-top: 1px solid white;
}
.MyOrderBgWhite .MyOrderBgWhite_child1 .MyOrderBgWhite_child1_select form #divselect ul li a {
  text-decoration: none;
  color: white;
  display: inline-block;
  width: 90%;
  height: 60%;
  padding-left: 10%;
}
.MyOrderBgWhite .MyOrderBgWhite_child1 .MyOrderBgWhite_child1_child3 {
  width: 20%;
  height: 90%;
  margin-left: 2%;
  float: left;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 100%;
  font-size: 14px;
}
.MyOrderBgWhite .MyOrderBgWhite_child1 .MyOrderBgWhite_child1_child3 span {
  margin-top: 2%;
}
.MyOrderBgWhite .MyOrderBgWhite_child2 {
  width: 97%;
  height: 9%;
  margin-left: 2%;
  display: flex;
  align-items: center;
}
.MyOrderBgWhite .MyOrderBgWhite_child2 span {
  float: left;
  width: 16%;
  height: 100%;
  margin-top: 0;
  display: inline-block;
  font-size: 13px;
  text-align: center;
}
.MyOrderBgWhite .MyOrderBgWhite_child2 :nth-child(1) {
  width: 14%;
  margin: 0;
}
.MyOrderBgWhite .MyOrderBgWhite_child2 :nth-child(2) {
  width: 14%;
}
.MyOrderBgWhite .MyOrderBgWhite_child2 :nth-child(3) {
  width: 14%;
}
.MyOrderBgWhite .MyOrderBgWhite_child3 {
  width: 96%;
  height: 51%;
  background-size: contain;
  margin: 0 auto;
  margin-top: 1%;
}
.MyOrderBgWhite .MyOrderBgWhite_child3 .orderlist {
  margin: 0;
  width: 100%;
  height: 18%;
  border: 1px solid transparent;
}
.MyOrderBgWhite .MyOrderBgWhite_child3 .orderlist div {
  float: left;
  display: flex;
  align-items: center;
  height: 100%;
  justify-content: center;
  color: #6f4934;
}
.MyOrderBgWhite .MyOrderBgWhite_child3 .orderlist :nth-child(1) {
  width: 14%;
}
.MyOrderBgWhite .MyOrderBgWhite_child3 .orderlist :nth-child(2) {
  width: 14%;
}
.MyOrderBgWhite .MyOrderBgWhite_child3 .orderlist :nth-child(3) {
  width: 15%;
}
.MyOrderBgWhite .MyOrderBgWhite_child3 .orderlist :nth-child(4) {
  width: 15%;
}
.MyOrderBgWhite .MyOrderBgWhite_child3 .orderlist :nth-child(5) {
  margin-left: 2%;
  width: 14%;
}
.MyOrderBgWhite .MyOrderBgWhite_child3 .orderlist :nth-child(6) {
  width: 24%;
  transform: scale(0.7, 1);
  left: 70%;
  position: absolute;
  margin-top: -22%;
}
.MyOrderBgWhite .MyOrderBgWhite_child3 .orderlist .shangfenBtn {
  width: 8%;
  margin-left: 18%;
  background: #b88f19;
  border-radius: 5px;
  height: 80%;
  color: white;
  font-weight: bold;
  text-shadow: 1px 0px 0px #bc8845, -1px 0px 0px #bc8845, 0px 1px 0px #bc8845, 0px -1px 0px #bc8845;
}
.MyOrderBgWhite .MyOrderBgWhite_child4 {
  width: 48%;
  height: 16%;
  margin: 0px auto;
  margin-top: 2px;
}
.MyOrderBgWhite .MyOrderBgWhite_child4 :nth-child(1) {
  float: left;
  margin: 0;
  width: 40%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: right;
}
.MyOrderBgWhite .MyOrderBgWhite_child4 :nth-child(2) {
  margin: 0;
  float: left;
  width: 20%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #8c5c4a;
}
.MyOrderBgWhite .MyOrderBgWhite_child4 :nth-child(3) {
  margin: 0;
  float: left;
  width: 40%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: left;
}
.MyOrder-child {
  position: relative;
  top: 10%;
