  position: relative;
  display: inline-block;
  text-align: center;
  padding-left: 2%;
  width: 25%;
  height: 25%;
  margin: auto;
  margin-top: 7.5%;
}
.MyOrder-child .MyOrder-child-child3 {
  float: left;
  position: relative;
  width: 50%;
  height: 35%;
  margin: auto;
  margin-top: 5%;
