  background: #9f7463;
}
.MyOrder-child .MyOrder-child-child3 :nth-child(1) {
  width: 90%;
  height: 100%;
  margin-left: 4%;
  font-size: 14px;
  color: white;
  background: transparent;
  border: transparent;
  outline: none;
}
.MyOrder-child .MyOrder-child-child4 {
  float: left;
  position: relative;
  display: inline-block;
  text-align: center;
  padding-left: 2%;
  width: 25%;
  height: 25%;
