  margin-top: 2%;
}
.MyOrder-child .MyOrder-child-child5 {
  float: left;
  position: relative;
  width: 50%;
  height: 30%;
  margin-top: 1%;
  margin-left: 2%;
  text-align: center;
  font-size: 14px;
  color: #8c5c4a;
  padding-left: 4%;
  display: flex;
  justify-content: start;
  align-items: center;
}
